import React, {useEffect, useState} from 'react';
import { Terminal } from 'xterm';
import { Modal } from 'antd';
import { Input } from 'antd';
import './test.css';
import { Card } from 'antd';
import { Row, Col } from 'antd';
import { Button,Space } from 'antd';
import {formatJson} from "../util/JsonUtil";
import { Switch } from 'antd';
import copy from 'copy-to-clipboard';
import { message } from 'antd';
const Code =props=>{
   const [ontStr,setOneStr]= useState();
    const {title,code,warn,other=[],copyStr} = props;
    return(<div>
        <p className="word-p">{title}</p>
        <p style={{color:'red'}}>{warn}</p>

        <div className="cnblogs_code">
            <p>{code}</p>
            {
                other.map(e=>(<p>{e}</p>))
            }
        </div>
        {
            copyStr&&
            <div>
                <Row>
                    <Col span={20}>

                        <Input onChange={e => setOneStr(e.target.value)}
                               placeholder={copyStr.placeholder}/>
                    </Col>
                    <Col span={1}>
                    </Col>
                    <Col span={2}>

                        <Button type="primary" onClick={() => {
                            copy(copyStr.beforeStr+ontStr+copyStr.afterStr);
                            message.success('');
                        }}></Button>
                    </Col>
                </Row>
            </div>
        }

    </div>);
};
